import { NextRequest, NextResponse } from 'next/server';
import { getCDPFacilitator, type CDPVerifyRequest } from '@/integrations/x402/cdp-facilitator';
import { parseX402Header } from '@/integrations/x402/payment-header-parser';

/**
 * POST /api/x402/verify
 * 
 * Verify x402 payment with CDP facilitator
 * 
 * Request body (one of):
 * - { paymentHeader: string } - x402 payment header from HTTP header
 * - { payment: string, paymentPayload: CDPPaymentPayload } - Direct payment payload
 */
export async function POST(request: NextRequest) {
  try {
    const body = await request.json();
    
    // Check if request body is already in CDP's expected format
    // CDP format: { x402Version: 2, paymentPayload, paymentRequirements }
    // paymentPayload and paymentRequirements are at root level (not nested)
    // See coinbase/x402 issue #577 about not losing requirements
    if (body.x402Version && body.paymentPayload && body.paymentRequirements) {
      // Request is already in CDP format - extract and convert to our internal format
      const cdpPaymentPayload = body.paymentPayload;
      const cdpPaymentRequirements = body.paymentRequirements; // At root level, not nested
      
      // Convert CDP format to our internal format
      // CDP uses payTo/maxAmountRequired, we need to convert to to/value
      const verifyRequest: CDPVerifyRequest = {
        payment: '', // Not needed when paymentPayload is provided directly
        paymentPayload: {
          x402Version: cdpPaymentPayload.x402Version || body.x402Version,
          scheme: cdpPaymentPayload.scheme || 'exact',
          network: cdpPaymentPayload.network || 'base',
          payload: cdpPaymentPayload.payload,
        },
        paymentRequirements: {
          scheme: cdpPaymentRequirements.scheme || 'exact',
          network: cdpPaymentRequirements.network || 'base',
          // Convert CDP field names to our internal format
          to: cdpPaymentRequirements.payTo || cdpPaymentRequirements.to,
          value: cdpPaymentRequirements.maxAmountRequired || cdpPaymentRequirements.value,
          resource: cdpPaymentRequirements.resource,
          validAfter: cdpPaymentRequirements.validAfter,
          validBefore: cdpPaymentRequirements.validBefore,
          asset: cdpPaymentRequirements.asset,
          // Optional fields
          ...(cdpPaymentRequirements.description && { description: cdpPaymentRequirements.description }),
          ...(cdpPaymentRequirements.mimeType && { mimeType: cdpPaymentRequirements.mimeType }),
        },
      };
      
      // Verify with CDP
      const cdp = getCDPFacilitator();
      const result = await cdp.verifyPaymentWithRetry(verifyRequest);
      
      if (result.success && result.valid) {
        return NextResponse.json({
          success: true,
          verification: {
            valid: true,
            transactionHash: result.transactionHash,
            kytStatus: result.kytStatus,
            ofacStatus: result.ofacStatus,
          },
          timestamp: new Date().toISOString(),
        });
      } else {
        return NextResponse.json(
          {
            success: false,
            error: result.error,
            verification: {
              valid: false,
              kytStatus: result.kytStatus,
              ofacStatus: result.ofacStatus,
            },
            errorDetails: result.errorDetails,
            timestamp: new Date().toISOString(),
          },
          { status: 400 }
        );
      }
    }
    
    // Legacy format support
    const { payment, paymentPayload, paymentHeader, resource, paymentRequirements: providedPaymentRequirements } = body;

    // Support both direct payment payload and payment header
    let verifyRequest: CDPVerifyRequest;

    if (paymentHeader) {
      // Parse payment header (just parse, don't verify yet)
      const parseResult = parseX402Header(paymentHeader);

      if (!parseResult.valid || !parseResult.parsed) {
        return NextResponse.json(
          {
            success: false,
            error: parseResult.error || 'Invalid payment header',
            timestamp: new Date().toISOString(),
          },
          { status: 400 }
        );
      }

      const parsed = parseResult.parsed;

      // Debug: Log parsed authorization to verify values
      console.log('[x402/verify] Parsed authorization:', {
        to: parsed.authorization.to,
        value: parsed.authorization.value,
        from: parsed.authorization.from,
        network: parsed.network,
      });

      // Use provided paymentRequirements if available, otherwise construct from parsed payment
      let paymentRequirements;
      
      if (providedPaymentRequirements) {
        // Use the explicitly provided paymentRequirements (from test script or client)
        paymentRequirements = providedPaymentRequirements;
      } else {
        // Construct paymentRequirements from parsed payment
        // CDP requires paymentRequirements to match the payment authorization
        // Get asset address from env or default to Base USDC
        const network = parsed.network || 'base';
        const assetAddress = process.env.USDC_ADDRESS || '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913'; // USDC on Base
        
        // Use provided resource or construct from request
        // If resource is provided, use it; otherwise use the hello-world endpoint as default
        const resourceUrl = resource || 
          (process.env.NEXT_PUBLIC_APP_URL || 
           (request.headers.get('x-forwarded-proto') === 'https' ? 'https' : 'http') + 
           '://' + (request.headers.get('host') || 'localhost:3001')) + 
          '/api/test/hello-world';
        
        // Build payment requirements matching CDP's expected schema
        // CDP expects: scheme, network, to, value, resource, validAfter, validBefore, asset
        // NOT: maxAmountRequired, payTo, payer, maxTimeoutSeconds, description, mimeType
        // Ensure all required fields are present
        if (!parsed.authorization) {
          return NextResponse.json(
            {
              success: false,
              error: 'Payment header missing authorization',
              timestamp: new Date().toISOString(),
            },
            { status: 400 }
          );
        }
        
        // Use authorization value directly (should already be in atomic units)
        // The authorization.value is the source of truth - it's what was signed
        const authValue = parsed.authorization.value;
        
        paymentRequirements = {
          scheme: 'exact',
          network,
          to: parsed.authorization.to, // CDP expects 'to', not 'payTo'
          value: authValue, // Always use authorization value (source of truth, should be atomic units)
          resource: resourceUrl,
          validAfter: parsed.authorization.validAfter || Math.floor(Date.now() / 1000).toString(),
          validBefore: parsed.authorization.validBefore || (Math.floor(Date.now() / 1000) + 300).toString(),
          asset: assetAddress,
          // DO NOT include optional fields like description, mimeType - CDP doesn't expect them
        };
      }
      
      // Validate required fields
      if (!paymentRequirements.to || !paymentRequirements.value || !paymentRequirements.resource || !paymentRequirements.asset) {
        return NextResponse.json(
          {
            success: false,
            error: 'Payment requirements missing required fields',
            missingFields: {
              to: !paymentRequirements.to,
              value: !paymentRequirements.value,
              resource: !paymentRequirements.resource,
              asset: !paymentRequirements.asset,
            },
            timestamp: new Date().toISOString(),
          },
          { status: 400 }
        );
      }

      verifyRequest = {
        payment: paymentHeader,
        paymentPayload: {
          x402Version: 1,
          scheme: 'exact', // CDP expects 'exact' scheme
          network: parsed.network || 'base',
          payload: {
            signature: parsed.signature,
            authorization: parsed.authorization,
          },
        },
        paymentRequirements,
      };
    } else if (payment && paymentPayload) {
      verifyRequest = { payment, paymentPayload };
    } else {
      return NextResponse.json(
        {
          success: false,
          error: 'Missing required fields: payment and paymentPayload, or paymentHeader',
          timestamp: new Date().toISOString(),
        },
        { status: 400 }
      );
    }

    // Verify with CDP
    const cdp = getCDPFacilitator();
    const result = await cdp.verifyPaymentWithRetry(verifyRequest);

    if (result.success && result.valid) {
      return NextResponse.json({
        success: true,
        verification: {
          valid: true,
          transactionHash: result.transactionHash,
          kytStatus: result.kytStatus,
          ofacStatus: result.ofacStatus,
        },
        timestamp: new Date().toISOString(),
      });
    } else {
      return NextResponse.json(
        {
          success: false,
          error: result.error,
          verification: {
            valid: false,
            kytStatus: result.kytStatus,
            ofacStatus: result.ofacStatus,
          },
          // Include error details for debugging
          errorDetails: result.errorDetails,
          timestamp: new Date().toISOString(),
        },
        { status: 400 }
      );
    }
  } catch (error) {
    console.error('[x402/verify] Error:', error);
    return NextResponse.json(
      {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error',
        timestamp: new Date().toISOString(),
      },
      { status: 500 }
    );
  }
}

/**
 * GET /api/x402/verify/health
 * 
 * Health check for CDP facilitator
 */
export async function GET(request: NextRequest) {
  try {
    const cdp = getCDPFacilitator();
    const health = await cdp.healthCheck();

    if (health.healthy) {
      return NextResponse.json({
        healthy: true,
        facilitator: 'cdp',
        timestamp: new Date().toISOString(),
      });
    } else {
      return NextResponse.json(
        {
          healthy: false,
          facilitator: 'cdp',
          error: health.error,
          timestamp: new Date().toISOString(),
        },
        { status: 503 }
      );
    }
  } catch (error) {
    return NextResponse.json(
      {
        healthy: false,
        facilitator: 'cdp',
        error: error instanceof Error ? error.message : 'Unknown error',
        timestamp: new Date().toISOString(),
      },
      { status: 503 }
    );
  }
}

