import { getCDPFacilitator, type CDPVerifyRequest } from './cdp-facilitator';
import type { PaymentAuthorization } from './signature-verifier';

/**
 * Parsed x402 payment header structure
 */
export interface ParsedPaymentHeader {
  invoiceId: string;
  txHash?: string;
  signature: string;
  authorization: PaymentAuthorization;
  network?: string;
}

/**
 * Parse x402 payment header from HTTP header value
 * 
 * x402 payment headers are typically base64-encoded JSON containing:
 * - signature: EIP-712 signature
 * - authorization: Payment authorization object
 * - network: Blockchain network identifier
 * 
 * Format: x402 <base64-encoded-json>
 */
export function parseX402Header(headerValue: string): {
  valid: boolean;
  parsed?: ParsedPaymentHeader;
  error?: string;
} {
  try {
    // Validate header is not empty
    if (!headerValue || headerValue.trim().length === 0) {
      return { valid: false, error: 'Payment header is empty' };
    }

    // Check for placeholder values
    if (headerValue.includes('<your_payment_header>') || headerValue.includes('<placeholder>')) {
      return { valid: false, error: 'Payment header contains placeholder value. Please provide a real x402 payment header.' };
    }

    // Remove 'x402 ' prefix if present
    const cleanHeader = headerValue.startsWith('x402 ')
      ? headerValue.slice(5).trim()
      : headerValue.trim();

    if (cleanHeader.length === 0) {
      return { valid: false, error: 'Payment header value is empty after removing prefix' };
    }

    // Decode base64 (works in both Node.js and browser environments)
    let decoded: string;
    try {
      decoded = typeof window === 'undefined'
        ? Buffer.from(cleanHeader, 'base64').toString('utf-8')
        : atob(cleanHeader);
    } catch (base64Error) {
      return { 
        valid: false, 
        error: `Invalid base64 encoding: ${base64Error instanceof Error ? base64Error.message : 'Failed to decode base64'}` 
      };
    }

    // Parse JSON
    let payload: {
      signature?: string;
      authorization?: {
        from?: string;
        to?: string;
        value?: string;
        validAfter?: string;
        validBefore?: string;
        nonce?: string;
      };
      network?: string;
      invoiceId?: string;
      txHash?: string;
    };
    
    try {
      payload = JSON.parse(decoded);
    } catch (jsonError) {
      return { 
        valid: false, 
        error: `Invalid JSON in payment header: ${jsonError instanceof Error ? jsonError.message : 'Failed to parse JSON'}. Decoded value: ${decoded.substring(0, 100)}` 
      };
    }

    // Validate required fields
    if (!payload.signature) {
      return { valid: false, error: 'Missing signature in payment header' };
    }

    if (!payload.authorization) {
      return { valid: false, error: 'Missing authorization in payment header' };
    }

    const auth = payload.authorization;
    if (!auth.from || !auth.to || !auth.value || !auth.nonce) {
      return { valid: false, error: 'Incomplete authorization fields' };
    }

    return {
      valid: true,
      parsed: {
        invoiceId: payload.invoiceId || auth.nonce,
        txHash: payload.txHash,
        signature: payload.signature,
        authorization: {
          from: auth.from as `0x${string}`,
          to: auth.to as `0x${string}`,
          value: auth.value,
          validAfter: auth.validAfter || Math.floor(Date.now() / 1000).toString(),
          validBefore: auth.validBefore || (Math.floor(Date.now() / 1000) + 300).toString(),
          nonce: auth.nonce,
        },
        network: payload.network || 'base',
      },
    };
  } catch (error) {
    return {
      valid: false,
      error: error instanceof Error ? error.message : 'Failed to parse payment header',
    };
  }
}

/**
 * Parse and verify x402 payment header with CDP facilitator
 * 
 * This function:
 * 1. Parses the payment header
 * 2. Verifies the payment with CDP
 * 3. Returns the verification result
 */
export async function parseAndVerifyPaymentHeader(
  headerValue: string
): Promise<{
  valid: boolean;
  payment?: ParsedPaymentHeader;
  transactionHash?: string;
  error?: string;
}> {
  try {
    // Parse the header
    const parseResult = parseX402Header(headerValue);
    if (!parseResult.valid || !parseResult.parsed) {
      return {
        valid: false,
        error: parseResult.error || 'Invalid payment header format',
      };
    }

    const parsed = parseResult.parsed;

    // Create CDP verify request
    const verifyRequest: CDPVerifyRequest = {
      payment: headerValue,
      paymentPayload: {
        x402Version: 1,
        scheme: 'x402',
        network: parsed.network || 'base',
        payload: {
          signature: parsed.signature,
          authorization: parsed.authorization,
        },
      },
    };

    // Verify with CDP
    const cdp = getCDPFacilitator();
    const verifyResult = await cdp.verifyPayment(verifyRequest);

    if (!verifyResult.valid || !verifyResult.success) {
      return {
        valid: false,
        error: verifyResult.error || 'Payment verification failed',
      };
    }

    return {
      valid: true,
      payment: parsed,
      transactionHash: verifyResult.transactionHash,
    };
  } catch (error) {
    return {
      valid: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

